import React from 'react'
import styled from 'styled-components'
import { AutoRow } from '../../components/Row'
import PoolItem from './PoolItem'
import { useLiquidityPools } from '../../state/farm/hooks'
import { HowToFarm } from '../common/index'
import { ColumnCenter } from 'components/Column'
import { TYPE } from 'theme'
import { useTranslation } from 'react-i18next'
import { PageLoading } from '../../components/PageLoading/index'
import { isMobile } from 'react-device-detect'

const PageWrapper = styled(AutoRow)`
  max-width: 1080px;
  width: 100%;
`

const MemoPoolItem = React.memo(PoolItem)

export default function FarmLiquidity() {
  const { t } = useTranslation()
  const pools = useLiquidityPools()
  return (
    <PageWrapper justify="center" gap="10px">
      {!isMobile &&
      <>
        <AutoRow justify="center">
          <TYPE.main color="#1733F7" fontSize={32}>{t('lpFarmTitle1')}</TYPE.main>
          <TYPE.main color="#000000" fontSize={32}>{t('lpFarmTitle2')}</TYPE.main>
        </AutoRow>
      </>
      }
      {pools && <HowToFarm />}
      {
        pools
        ? pools.map(i => <MemoPoolItem key={i.lpAddress} poolInfo={i}/>)
        :
        <ColumnCenter style={{marginTop: '500px'}}>
          <AutoRow justify="center">
            <PageLoading />
          </AutoRow>
        </ColumnCenter>
      }
    </PageWrapper>
  )
}