import { useCallback, useEffect, useState } from 'react'
import { useActiveWeb3React } from '../../hooks'
import useDebounce from '../../hooks/useDebounce'
import useIsWindowVisible from '../../hooks/useIsWindowVisible'
import { updateBlockNumber } from './actions'
import { useDispatch } from 'react-redux'

const BLOCK_INTERVAL_STATS_COUNT = 2560
const REFRESH_INTERVAL = 3000

export default function Updater(): null {
  const { library, chainId } = useActiveWeb3React()
  const dispatch = useDispatch()

  const windowVisible = useIsWindowVisible()

  const [state, setState] = useState<{ chainId: number | undefined; blockNumber: number | null, blockInterval: number | null}>({
    chainId,
    blockNumber: null,
    blockInterval: null,
  })

  const blockNumberCallback = useCallback(
    (blockNumber: number, blockInterval: number) => {
      setState(state => {
        if (chainId === state.chainId) {
          if (typeof state.blockNumber !== 'number') return { chainId, blockNumber, blockInterval }
          return { chainId, blockNumber: Math.max(blockNumber, state.blockNumber), blockInterval }
        }
        return state
      })
    },
    [chainId, setState]
  )

  // attach/detach listeners
  useEffect(() => {
    if (!library || !chainId || !windowVisible) return undefined

    setState({ chainId, blockNumber: null, blockInterval: null })

    const getBlockInterval = (lastBlockNumber: number) => {
      return Promise.all([
        library.getBlock(lastBlockNumber),
        library.getBlock(Math.max(lastBlockNumber - BLOCK_INTERVAL_STATS_COUNT, 1))
      ])
      .then(results => {
        const [t1, t2] = results
        if (t1 && t2 && t1.timestamp && t2.timestamp) {
          const blockInterval = (t1.timestamp - t2.timestamp) / BLOCK_INTERVAL_STATS_COUNT
          console.log('blockInterval:', blockInterval, lastBlockNumber)
          blockNumberCallback(lastBlockNumber, blockInterval)
        }
      })
      .catch(error => {
        // console.log(`Failed to get block number for chainId: ${chainId}`, error)
        blockNumberCallback(lastBlockNumber, 3.5)
      })
    }

    library
      .getBlockNumber()
      .then(getBlockInterval)

    const timer = setInterval(() => {
      library
      .getBlockNumber()
      .then(getBlockInterval)
    }, REFRESH_INTERVAL)
    return () => {
      clearInterval(timer)
    }
  }, [dispatch, chainId, library, blockNumberCallback, windowVisible])

  const debouncedState = useDebounce(state, 100)

  useEffect(() => {
    if (!debouncedState.chainId || !debouncedState.blockNumber || !debouncedState.blockInterval || !windowVisible) return
    dispatch(updateBlockNumber({
      chainId: debouncedState.chainId,
      blockNumber: debouncedState.blockNumber,
      blockInterval: debouncedState.blockInterval,
    }))
  }, [windowVisible, dispatch, debouncedState.blockNumber, debouncedState.chainId, debouncedState.blockInterval])

  return null
}
