import React from 'react'
import styled from 'styled-components'
import { AutoRow } from 'components/Row'
import PoolItem from './PoolItem'
import { useDepositPools } from '../../state/farm/hooks'
import { HowToFarm } from '../common/index'
import { ColumnCenter } from 'components/Column'
import { TYPE } from 'theme'
import { useTranslation } from 'react-i18next'
import { AutoColumn } from '../../components/Column/index'
import { PageLoading } from '../../components/PageLoading/index'
import { isMobile } from 'react-device-detect'

const PageWrapper = styled(AutoColumn)`
  max-width: 740px;
  width: 100%;
`

const MemoPoolItem = React.memo(PoolItem)

export default function FarmDeposit() {
  const { t } = useTranslation()
  const pools = useDepositPools()
  return (
    <PageWrapper justify="center" gap="10px">
      {!isMobile &&
        <AutoRow justify="center">
          <TYPE.mediumHeader fontSize={36}>{t('depositTitle1')}</TYPE.mediumHeader>
          <TYPE.mediumHeader fontSize={36} color="#14AC22" marginLeft="5px" marginRight="5px">{t('depositTitle2')}</TYPE.mediumHeader>
          <TYPE.mediumHeader fontSize={36}>{t('depositTitle3')}</TYPE.mediumHeader>
        </AutoRow>
      }
      {pools && <HowToFarm />}
      <AutoRow width="100%" gap="10px">
        {
          pools
          ? pools.map(i => <MemoPoolItem key={i.tokenAddress} poolInfo={i} index={Number(i.pid)}/>)
          :
          <ColumnCenter style={{marginTop: '500px'}}>
            <AutoRow justify="center">
              <PageLoading />
            </AutoRow>
          </ColumnCenter>
        }
      </AutoRow>
    </PageWrapper>
  )
}