import { useMemo } from 'react'
import { TokenAmount, Token, JSBI, CurrencyAmount, Percent } from '@kswap-finance/sdk';
import useUSDCPrice from '../utils/useUSDCPrice';
import { useTotalSupply } from '../data/TotalSupply'
import { usePair } from '../data/Reserves'
import { useActiveWeb3React } from './index'
import { KST } from '../constants/index'

export function useLiquidityTokenValue(stakedAmount?: TokenAmount, token0?: Token, token1?: Token): CurrencyAmount | undefined {
  const [, pair] = usePair(token0, token1)
  const totalSupply = useTotalSupply(stakedAmount?.token)
  const token1Price = useUSDCPrice(token1)
  return useMemo(() => {
    if (!pair || !totalSupply || !token1Price || !stakedAmount || !token0 || !token1) {
      return undefined
    }
    const stakedValueInToken1 = new TokenAmount(
      token1,
      JSBI.divide(
        JSBI.multiply(
          JSBI.multiply(stakedAmount.raw, pair.reserveOf(token1).raw),
          JSBI.BigInt(2)
        ),
        totalSupply.raw
      )
    )
    return token1Price.quote(stakedValueInToken1)
  }, [pair, totalSupply, token1Price, stakedAmount, token0, token1])
}

export function useTokenValue(amount: CurrencyAmount | undefined): CurrencyAmount | undefined {
  const tokenPrice = useUSDCPrice(amount ?  amount.currency : undefined)
  return useMemo(() => {
    if (!tokenPrice || !amount) return undefined
    return tokenPrice.quote(amount)
  }, [tokenPrice, amount])
}

export function useAPY(amount: CurrencyAmount | undefined, weeklyRewards: CurrencyAmount): Number | undefined {
  const { chainId } = useActiveWeb3React()
  const rewardPrice = useUSDCPrice(chainId ?KST[chainId] : undefined)
  const stakedPrice = useUSDCPrice(amount ?  amount.currency : undefined)

  return useMemo(() => {
    if (!chainId || !rewardPrice || !amount || !stakedPrice) return undefined
    if (JSBI.equal(amount.raw, JSBI.BigInt(0))) return undefined

    const totalRewardsValue = rewardPrice.quote(weeklyRewards)
    const totalStakedValue = stakedPrice.quote(amount)
    const weeklyAPY = new Percent(totalRewardsValue.raw, totalStakedValue.raw)
    return Number(weeklyAPY.toFixed(8))
  }, [chainId, rewardPrice, amount, stakedPrice, weeklyRewards])
}