import React, { useState } from 'react'
import ReactGA from 'react-ga'
import styled from 'styled-components'
import { TokenAmount } from '@kswap-finance/sdk'
import { AutoRow } from '../../components/Row'
import PoolItem from './PoolItem'
import { useTradingPools } from '../../state/farm/hooks'
import Card from 'components/Card'
import { AutoColumn, ColumnCenter } from 'components/Column'
import { TYPE } from '../../theme'
import { ButtonSecondary } from '../../components/Button/index'
import { calculateGasMargin, getTradingPoolContract } from '../../utils'
import { useActiveWeb3React } from '../../hooks'
import { useTransactionAdder } from '../../state/transactions/hooks'
import { useTranslation } from 'react-i18next'
import { HowToFarm } from '../common/index'
import { Dots } from 'components/swap/styleds'
import { PageLoading } from '../../components/PageLoading/index'

const PageWrapper = styled(AutoRow)`
  max-width: 1080px;
  width: 100%;
`

const HeaderCard = styled(Card)`
  background-image: linear-gradient(150deg, #5245FF 0%, #062EF5 100%);
  box-shadow: 0 16px 32px -4px rgba(6,46,245,0.16);
`

const MemoPoolItem = React.memo(PoolItem)

export default function FarmTrade() {
  const { account, chainId, library } = useActiveWeb3React()
  const { t } = useTranslation()
  const addTransaction = useTransactionAdder()
  const pools = useTradingPools()
  const [submitting, setSubmitting] = useState(false)

  let totalRewards: TokenAmount | undefined
  if (pools && pools[0] && pools[0].userRewardsUnclaimed) {
    totalRewards = pools[0].userRewardsUnclaimed
    for (let i = 1; i < pools.length; i++) {
      if (pools[i].userRewardsUnclaimed) {
        totalRewards = totalRewards.add(pools[i].userRewardsUnclaimed!)
      }
    }
  }

  async function onHarvestAll() {
    if (!chainId || !account || !library || !totalRewards) return

    const poolContract = getTradingPoolContract(chainId, library, account)
    const estimate = poolContract.estimateGas.harvestAll
    const method = poolContract.harvestAll
    const args: any[] = []
  
    setSubmitting(true)
    try {
      const estimatedGasLimit = await estimate(...args, {})
      const gasLimit = calculateGasMargin(estimatedGasLimit)
      const response = await method(...args, { gasLimit })
      
      addTransaction(response, {
        summary: `Harvest  ${totalRewards.toSignificant()} KST from trading pool`
      })
      ReactGA.event({
        category: 'Farm',
        action: 'HarvestAllTrading',
        label: totalRewards.toSignificant()
      })
    } catch (e) {
      console.log('failed to withdraw:', e)
    }
    setSubmitting(false)
  } 

  return (
    <PageWrapper justify="center" gap="10px">
      <HeaderCard maxWidth="710px" width="100%" padding="24px" borderRadius="12px">
        <AutoColumn justify="center" gap="10px">
          <TYPE.white fontWeight={400} fontSize={16}>{t('currentRewards')}</TYPE.white>
          <TYPE.white fontWeight={600} fontSize={30}>{totalRewards ? totalRewards.toSignificant() : '0'} KST</TYPE.white>
          <ButtonSecondary
              color="white"
              border="1px solid white"
              width="180px"
              height="50px"
              disabled={submitting || !totalRewards || totalRewards.equalTo('0')}
              onClick={onHarvestAll}>
                {submitting ? <Dots>{t('claimAll')}</Dots> : t('claimAll')}
            </ButtonSecondary>
        </AutoColumn>
      </HeaderCard>
      {pools && <HowToFarm />}
      {pools
        ? pools.map(i => <MemoPoolItem key={i.pair} poolInfo={i}/>)
        :
        <ColumnCenter style={{marginTop: '500px'}}>
          <AutoRow justify="center">
            <PageLoading />
          </AutoRow>
        </ColumnCenter>
      }
    </PageWrapper>
  )
}