import { ChainId, TokenAmount } from '@kswap-finance/sdk'
import React, { useMemo } from 'react'
import { X } from 'react-feather'
import styled from 'styled-components'
import tokenLogo from '../../assets/svg/token-logo.svg'
import { KST } from '../../constants'
import { useTotalSupply } from '../../data/TotalSupply'
import { useActiveWeb3React } from '../../hooks'
import { useMerkleDistributorContract } from '../../hooks/useContract'
import useCurrentBlockTimestamp from '../../hooks/useCurrentBlockTimestamp'
import { useAggregateUniBalance, useTokenBalance } from '../../state/wallet/hooks'
import { TYPE, UniTokenAnimated } from '../../theme'
import { computeUniCirculation } from '../../utils/computeUniCirculation'
import useUSDCPrice from '../../utils/useUSDCPrice'
import { AutoColumn } from '../Column'
import { RowBetween } from '../Row'
import { CardSection } from '../earn/styled'
import { useTranslation } from 'react-i18next'

const ContentWrapper = styled(AutoColumn)`
  width: 100%;
`

const ModalUpper = styled(AutoColumn)`
  background: #FFFFFF;
  background-image: linear-gradient(270deg, #F1E2FE 0%, #F3FDDF 100%), linear-gradient(0deg, #FFFFFF 0%, rgba(255,255,255,0.00) 100%);
  border-radius: 20px;
  padding: 0.5rem;
  width: 100%;
  position: relative;
  overflow: hidden;
`

export const StyledClose = styled(X)`
  position: absolute;
  right: 16px;
  top: 16px;

  :hover {
    cursor: pointer;
  }
`

const SectionBreak = styled.div`
  height: 1px;
  width: 100%;
  opacity: 0.06;
  background: #1733F7;
  margin-top: 36px;
`

/**
 * Content for balance stats modal
 */
export default function UniBalanceContent({ setShowUniBalanceModal }: { setShowUniBalanceModal: any }) {
  const { account, chainId } = useActiveWeb3React()
  const { t } = useTranslation()
  const uni = chainId ? KST[chainId] : undefined

  const total = useAggregateUniBalance()
  // const uniBalance: TokenAmount | undefined = useTokenBalance(account ?? undefined, uni)

  const totalSupply: TokenAmount | undefined = useTotalSupply(uni)
  const uniPrice = useUSDCPrice(uni)
  const blockTimestamp = useCurrentBlockTimestamp()
  const unclaimedUni = useTokenBalance(useMerkleDistributorContract()?.address, uni)
  const circulation: TokenAmount | undefined = useMemo(
    () =>
      blockTimestamp && uni && chainId === ChainId.MAINNET
        ? computeUniCirculation(uni, blockTimestamp, unclaimedUni)
        : totalSupply,
    [blockTimestamp, chainId, totalSupply, unclaimedUni, uni]
  )

  return (
    <ContentWrapper gap="lg">
      <ModalUpper>
        <CardSection gap="md">
          <RowBetween>
            <TYPE.black></TYPE.black>
            <StyledClose stroke="black" onClick={() => setShowUniBalanceModal(false)} />
          </RowBetween>
        </CardSection>
        {/* <Break /> */}
        {account && (
          <>
            <CardSection gap="sm">
              <AutoColumn gap="md" justify="center">
                <UniTokenAnimated width="48px" src={tokenLogo} />{' '}
                <TYPE.black fontSize={36} fontWeight={600}>
                  {total?.toFixed(2, { groupSeparator: ',' })}
                </TYPE.black>
              </AutoColumn>
              {/* <AutoColumn gap="md">
                <RowBetween>
                  <TYPE.white color="white">Balance:</TYPE.white>
                  <TYPE.white color="white">{uniBalance?.toFixed(2, { groupSeparator: ',' })}</TYPE.white>
                </RowBetween>
              </AutoColumn> */}
            </CardSection>
            <SectionBreak />
          </>
        )}
        <CardSection gap="sm">
          <AutoColumn gap="md">
            <RowBetween>
              <TYPE.text2>{`KST ${t('price')}`}</TYPE.text2>
              <TYPE.text2>${uniPrice?.toFixed(2) ?? '-'}</TYPE.text2>
            </RowBetween>
            <RowBetween>
              <TYPE.text2>{`KST ${t('inCirculation')}`}</TYPE.text2>
              <TYPE.text2>{circulation?.toFixed(0, { groupSeparator: ',' })}</TYPE.text2>
            </RowBetween>
            <RowBetween>
              <TYPE.text2>{`KST ${t('totalSupply')}`}</TYPE.text2>
              <TYPE.text2>{totalSupply?.toFixed(0, { groupSeparator: ',' })}</TYPE.text2>
            </RowBetween>
            {/* {uni && uni.chainId === ChainId.MAINNET ? (
              <ExternalLink href={`https://uniswap.info/token/${uni.address}`}>View KST Analytics</ExternalLink>
            ) : null} */}
          </AutoColumn>
        </CardSection>
      </ModalUpper>
    </ContentWrapper>
  )
}
