import React, { useCallback, useEffect, useMemo, useState } from 'react'
import styled from 'styled-components'
import ReactGA from 'react-ga'
import { useLocation, Link, NavLink } from 'react-router-dom'
import { ArrowLeft, PlusCircle } from 'react-feather'
import { darken } from 'polished'
import { TokenAmount, Percent, Token } from '@kswap-finance/sdk'
import { useActiveWeb3React } from '../../hooks'
import { useSignleLiquidityPool, useLiquidityPoolUserInfo } from '../../state/farm/hooks'
// import { useLiquidityTokenValue } from '../../hooks/useValue'
import { calculateGasMargin, getLiquidityPoolContract } from '../../utils/index'
import { AutoColumn } from '../../components/Column/index'
import { TYPE } from '../../theme'
import { AutoRow, RowBetween } from 'components/Row'
import Loader from 'components/Loader'
import CurrencyInputPanel from '../../components/CurrencyInputPanel/index'
import { ApprovalState, useApproveCallback } from 'hooks/useApproveCallback'
import { ButtonConfirmed, ButtonError, ButtonLight } from 'components/Button'
import { useTransactionAdder } from '../../state/transactions/hooks'
import { LightCard } from '../../components/Card/index'
import QuestionHelper from 'components/QuestionHelper'
import { RowFixed } from '../../components/Row/index'
import { useTranslation } from 'react-i18next'
import { tryParseAmount } from '../../state/swap/hooks'
import { Dots } from 'components/swap/styleds'

export const Separator = styled.div`
  width: 100%;
  height: 1px;
  margin: 8px 0px 8px 0px;
  background-color: ${({ theme }) => theme.bg3};
`

const StyledArrowLeft = styled(ArrowLeft)`
  color: ${({ theme }) => theme.text1};
`

const activeClassName = 'ACTIVE'

const StyledNavLink = styled(NavLink).attrs({
  activeClassName
})`
  ${({ theme }) => theme.flexRowNoWrap}
  align-items: left;
  outline: none;
  cursor: pointer;
  text-decoration: none;

  &.${activeClassName} {
    font-weight: 600;
  }

  :hover,
  :focus {
    color: ${({ theme }) => darken(0.1, '#062EF5')};
  }
`

enum SubmittingOperation {
  NONE,
  HARVEST,
  WITHDRAW,
  STAKE,
}

export default function FarmLiquidityDetail() {
  const { account, chainId, library } = useActiveWeb3React()
  const { t } = useTranslation()
  const addTransaction = useTransactionAdder()

  const { pathname } = useLocation()
  const lpAddress = pathname.split('/').pop()
  const poolInfo = useSignleLiquidityPool(lpAddress)
  const userInfo = useLiquidityPoolUserInfo(lpAddress)
  const stakedAmount = (poolInfo && userInfo) ? new TokenAmount(poolInfo.lpToken, userInfo.stakedAmount) : undefined
  // const stakedValue = useLiquidityTokenValue(stakedAmount, poolInfo?.token0, poolInfo?.token1)
  const stakedRatio = (poolInfo && stakedAmount) ? new Percent(stakedAmount.raw, poolInfo.totalStaked.raw) : undefined
  const balance = (poolInfo && userInfo) ? new TokenAmount(poolInfo.lpToken, userInfo.lpBalance) : undefined

  const [typedValue, setTypedValue] = useState('0')
  const onUserInput = useCallback((typedValue: string) => {
    setTypedValue(typedValue)
  }, [])

  const typedAmount = useMemo(() => {
    if (!poolInfo) return undefined
    return tryParseAmount(typedValue, poolInfo?.lpToken)
  }, [typedValue, poolInfo])

  const [submitting, setSubmitting] = useState(SubmittingOperation.NONE)

  const [approval, approveCallback] = useApproveCallback(typedAmount, poolInfo?.poolAddress)

  const [approvalSubmitted, setApprovalSubmitted] = useState<boolean>(false)
  useEffect(() => {
    if (approval === ApprovalState.PENDING) {
      setApprovalSubmitted(true)
    }
  }, [approval, approvalSubmitted])

  const showApproveFlow =
    approval === ApprovalState.NOT_APPROVED ||
    approval === ApprovalState.PENDING ||
    (approvalSubmitted && approval === ApprovalState.APPROVED)

  const symbolOrAddress = (token: Token) => token.symbol === 'OKT' ? 'OKT' : token.address
  const addLiquiityUrl =  poolInfo ? `/add/${symbolOrAddress(poolInfo.token0)}/${symbolOrAddress(poolInfo.token1)}` :  '/add'

  async function onStake() {
    if (!chainId || !account || !library || !poolInfo || !typedAmount) return

    const poolContract = getLiquidityPoolContract(chainId, library, account)
    const estimate = poolContract.estimateGas.deposit
    const method = poolContract.deposit
    const args = [poolInfo.pid, `0x${typedAmount.raw.toString(16)}`]
    console.log('stake lp token args', args)
  
    setSubmitting(SubmittingOperation.STAKE)
    try {
      const estimatedGasLimit = await estimate(...args, {})
      const gasLimit = calculateGasMargin(estimatedGasLimit)
      const response = await method(...args, { gasLimit })
      
      addTransaction(response, {
        summary: `Stake ${typedValue} LP Token`
      })
      ReactGA.event({
        category: 'Farm',
        action: 'Stake',
        label: [poolInfo.token0.symbol, poolInfo.token1.symbol].join('/')
      })
      setTypedValue('0')
    } catch (e) {
      console.log('failed to stake:', e)
    }
    setSubmitting(SubmittingOperation.NONE)
  }

  async function onWithdraw(isExit: boolean) {
    if (!chainId || !account || !library || !poolInfo || !stakedAmount || !userInfo) return

    const poolContract = getLiquidityPoolContract(chainId, library, account)
    const estimate = poolContract.estimateGas.withdraw
    const method = poolContract.withdraw
    const args = [poolInfo.pid, isExit ? `0x${stakedAmount.raw.toString(16)}` : '0']
    console.log('withdraw args', args)
  
    setSubmitting(isExit ? SubmittingOperation.WITHDRAW : SubmittingOperation.HARVEST)
    try {
      const estimatedGasLimit = await estimate(...args, {})
      const gasLimit = calculateGasMargin(estimatedGasLimit)
      const response = await method(...args, { gasLimit })
      
      addTransaction(response, {
        summary: isExit
          ? `Withdraw ${stakedAmount.toSignificant(8)} LP(${poolInfo.token0.symbol + '/' + poolInfo.token1.symbol})`
          : `Harvest  ${userInfo.unclaimedRewards.toSignificant(8)} KST from liquidity pool`
      })
      ReactGA.event({
        category: 'Farm',
        action: 'Withdraw',
        label: [poolInfo.token0.symbol, poolInfo.token1.symbol].join('/')
      })
    } catch (e) {
      console.log('failed to withdraw:', e)
    }
    setSubmitting(SubmittingOperation.NONE)
  }
 
  return (
    <LightCard width="100%" maxWidth="420px">
      <AutoColumn gap="16px">
        <RowBetween>
          <Link to="/farm/liquidity"><StyledArrowLeft /></Link>
          <TYPE.mediumHeader>{t('liquidityFarm')}</TYPE.mediumHeader>
          <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
        </RowBetween>
        <AutoRow justify="center">
          {
            poolInfo
            ? <TYPE.small>LP({ poolInfo.token0.symbol + '/' + poolInfo.token1.symbol })</TYPE.small>
            : <Loader />
          }
        </AutoRow>
        <RowBetween>
          <RowFixed>
            <TYPE.main>{t('myRewards')}</TYPE.main>
            <QuestionHelper text={t('claimTips')}></QuestionHelper>
          </RowFixed>
          {
            userInfo
            ? <TYPE.main>{ userInfo.unclaimedRewards.toSignificant(8) } KST</TYPE.main>
            : <Loader />
          }
        </RowBetween>
        <ButtonLight
          id="harvest-button"
          disabled={!userInfo || userInfo.unclaimedRewards.equalTo('0') || submitting !== SubmittingOperation.NONE}
          onClick={() => onWithdraw(false)}
        >
           {
            submitting === SubmittingOperation.HARVEST
            ? <Dots>{t('harvest')}</Dots>
            : t('harvest')
          }
        </ButtonLight>
        <Separator />
        <RowBetween>
          <RowFixed>
            <TYPE.main>{t('myStaking')}</TYPE.main>
            <QuestionHelper text={t('stakeTips')}></QuestionHelper>
          </RowFixed>
          {
            ( stakedAmount && stakedRatio)
            ? <TYPE.main>{ stakedAmount.toSignificant(6) } ({ stakedRatio.toSignificant(4) }%)</TYPE.main>
            : <Loader />
          }
        </RowBetween>
        <ButtonLight
          id="withdraw-button"
          disabled={!stakedAmount || stakedAmount.equalTo('0') || submitting !== SubmittingOperation.NONE}
          onClick={() => onWithdraw(true)}
        >
           {
            submitting === SubmittingOperation.WITHDRAW
            ? <Dots>{t('withdraw')}</Dots>
            : t('withdraw')
          }
        </ButtonLight>
        <Separator />
        <StyledNavLink to={addLiquiityUrl}>
            <RowFixed>
              <PlusCircle size={16} />
              <TYPE.main color="#062EF5" fontSize={14} marginLeft="5px">{t('addLiquidity')}</TYPE.main>
            </RowFixed>
        </StyledNavLink>
        <CurrencyInputPanel
            id="stake-liquidity-token"
            label={t('input')}
            value={ typedValue }
            hideInput={ false }
            currency={ poolInfo?.lpToken }
            showMaxButton={ true }
            disableCurrencySelect={ true }
            onUserInput={ onUserInput }
            onMax={ () => onUserInput(balance?.toExact() || '0') }
        />
        {showApproveFlow && (
          <ButtonConfirmed
            onClick={approveCallback}
            disabled={approval !== ApprovalState.NOT_APPROVED || approvalSubmitted}
            altDisabledStyle={approval === ApprovalState.PENDING}
            confirmed={approval === ApprovalState.APPROVED}
          >
            {
              approval === ApprovalState.PENDING ? (
                <AutoRow gap="6px" justify="center">
                  {t('approving')} <Loader stroke="white" />
                </AutoRow>
              ) : approvalSubmitted && approval === ApprovalState.APPROVED ? (
                t('approved')
              ) : (
                t('approve')
              )
            }
          </ButtonConfirmed>
        )}
        <ButtonError
          id="stake-button"
          disabled={approval !== ApprovalState.APPROVED || submitting !== SubmittingOperation.NONE}
          error={ false }
          onClick={ onStake }
        >
           {
            submitting === SubmittingOperation.STAKE
            ? <Dots>{t('stake')}</Dots>
            : t('stake')
          }
        </ButtonError>
      </AutoColumn>
    </LightCard>
  )
}