import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import { ButtonLight } from '../../components/Button'
import { JitterCard } from '../../components/Card'
import { AutoRow, RowBetween } from '../../components/Row'
import { AutoColumn } from '../../components/Column'
import DoubleCurrencyLogo from '../../components/DoubleLogo'
import { TYPE } from '../../theme'
import { LiquidityPoolInfo } from '../../state/farm/hooks'
import { useLiquidityTokenValue , useAPY } from '../../hooks/useValue'
import { useTranslation } from 'react-i18next'
import { ClosedTag } from '../common'
import { isMobile } from 'react-device-detect'

const Wrapper = styled.div`
  position: relative;
  width: 340px;

  ${({ theme }) => theme.mediaWidth.upToSmall`
  width: 100% !important;
  `};
`

const RoundedBox = styled.div`
  border: 1px solid rgb(6, 46, 245, 0.2);
  border-radius: 20px;
  padding: 2px 12px;
  color: #062EF5;
  margin: 16px 0 16px 0;
`

export default function PoolItem({ poolInfo }: { poolInfo: LiquidityPoolInfo }) {
  const { t } = useTranslation()
  const { token0, token1, totalStaked, weeklyRewards } = poolInfo
  const tvl = useLiquidityTokenValue(totalStaked, token0, token1)
  const apy = useAPY(tvl, weeklyRewards)
  return (
    <Wrapper>
      {poolInfo.closed && <ClosedTag />}
      <JitterCard width="100%" padding="30px 24px 24px" mobile={isMobile}>
          <AutoColumn gap="sm">
            <DoubleCurrencyLogo currency0={token0} currency1={token1} margin={true} size={36} />
            <AutoRow justify="center">
              <TYPE.mediumHeader>LP({ token0.symbol + '/' + token1.symbol })</TYPE.mediumHeader>
            </AutoRow>
            <AutoRow justify="center">
              <RoundedBox>{t('apy') + ':'} {apy ? (Number(apy) * 365 / 7).toFixed(2) : '-'}%</RoundedBox>
            </AutoRow>
            <RowBetween>
              <TYPE.main>TVL</TYPE.main>
              <TYPE.main>${ tvl ? tvl.toFixed(0) : '-' }</TYPE.main>
            </RowBetween>
            <RowBetween>
              <TYPE.main>{t('weeklyRewards')}</TYPE.main>
              <TYPE.main>{ weeklyRewards.toFixed(0) } KST</TYPE.main>
            </RowBetween>
            <ButtonLight
              as={Link} to={ '/farm/liquidity/' + poolInfo.lpAddress}
              style={{marginTop: '20px', borderRadius: '12px', padding: '14px' }}
            >
              {t('select')}
            </ButtonLight>
          </AutoColumn>
        </JitterCard>
      </Wrapper>
  )
}