import React from 'react'
import styled from 'styled-components'
import { JitterCard } from '../../components/Card'
import { AutoRow, RowBetween } from '../../components/Row'
import { AutoColumn } from '../../components/Column'
import DoubleCurrencyLogo from '../../components/DoubleLogo'
import { TYPE } from '../../theme'
import { TradingPoolInfo } from '../../state/farm/hooks';
// import { useLiquidityTokenValue , useAPY } from '../../hooks/useValue'
// import Loader from '../../components/Loader/index'
import { useActiveWeb3React } from '../../hooks/index'
import { useAPY } from '../../hooks/useValue'
import { useTranslation } from 'react-i18next'
import { ClosedTag } from '../common'
import { Countdown } from 'pages/Earn/Countdown'
import { useBlockInterval, useBlockNumber } from '../../state/application/hooks'
import { isMobile } from 'react-device-detect'

const Wrapper = styled.div`
  position: relative;
  width: 340px;

  ${({ theme }) => theme.mediaWidth.upToSmall`
  width: 100% !important;
  `};
`

const Separator = styled.div`
  width: 100%;
  height: 1px;
  margin: 8px 0px 8px 0px;
  background-color: ${({ theme }) => theme.bg3};
  `

  const RoundedBox = styled.div`
    border: 1px solid rgb(6, 46, 245, 0.2);
    border-radius: 20px;
    padding: 2px 12px;
    color: #062EF5;
    margin: 16px 0 16px 0;
`

export default function PoolItem({ poolInfo }: { poolInfo: TradingPoolInfo }) {
  const { t } = useTranslation()
  const { token0, token1, quantity, userAccRewards, weeklyRewards, allocatedRewards, userRewardsUnclaimed, userQuantity } = poolInfo
  const { account } = useActiveWeb3React()
  let apy = useAPY(quantity, weeklyRewards)
  if (apy) {
    apy = Number(apy) * 365 / 7 / 0.003
  }
  const startBlock = 1145885
  const blockInterval = useBlockInterval()
  const lastBlock = useBlockNumber()
  const targetTime = typeof lastBlock !== 'undefined'
    ? Math.floor((startBlock - lastBlock) * blockInterval + Date.now() / 1000)
    : undefined
  return (
    <Wrapper>
      {poolInfo.closed && <ClosedTag />}
      <JitterCard width="100%" borderRadius="20px" padding="30px 1.25em" mobile={isMobile}>
          <AutoColumn gap="sm" justify="center">
            {(lastBlock && targetTime && startBlock >= lastBlock) &&
              <Countdown targetTime={targetTime} message={t('countdown')} />
            }
            <DoubleCurrencyLogo currency0={token0} currency1={token1} margin={true} size={36} />
            <AutoRow justify="center">
              <TYPE.mediumHeader>{ token0.symbol + '/' + token1.symbol }</TYPE.mediumHeader>
            </AutoRow>
            <AutoRow justify="center">
              <RoundedBox>{t('apy') + ':'} {apy ? apy.toFixed(2) : '-'}%</RoundedBox>
            </AutoRow>
            <RowBetween>
              <TYPE.main>{t('rewardsAllocated')}</TYPE.main>
              <TYPE.main>{ allocatedRewards.toFixed(0) } KST</TYPE.main>
            </RowBetween>
            <RowBetween>
              <TYPE.main>{t('totalVolume')}</TYPE.main>
              <TYPE.main>${quantity.toFixed(0)}</TYPE.main>
            </RowBetween>
            { account && (
              <>
                <Separator />
                <RowBetween>
                  <TYPE.main>{t('myRewards')}</TYPE.main>
                  <TYPE.main color="#FF7F2A">{userRewardsUnclaimed ? userRewardsUnclaimed.toSignificant() : '0'} KST</TYPE.main>
                </RowBetween>
                <RowBetween>
                  <TYPE.main>{t('myTotalRewards')}</TYPE.main>
                  <TYPE.main>{userAccRewards ? userAccRewards.toSignificant() : '0'} KST</TYPE.main>
                </RowBetween>
                <RowBetween>
                  <TYPE.main>{t('myVolume')}</TYPE.main>
                  <TYPE.main>${userQuantity ? userQuantity.toSignificant() : '0'}</TYPE.main>
                </RowBetween>
              </>
            )}
          </AutoColumn>
        </JitterCard>
      </Wrapper>
  )
}