import React, { useRef } from 'react'
import { BookOpen, Code, MessageCircle } from 'react-feather'
import styled from 'styled-components'
import MenuIcon from '../../assets/images/menu.svg'
import { useOnClickOutside } from '../../hooks/useOnClickOutside'
import { ApplicationModal } from '../../state/application/actions'
import { useModalOpen, useToggleModal } from '../../state/application/hooks'
import { ExternalLink } from '../../theme'
import i18next from '../../i18n'

const StyledMenuButton = styled.button`
  width: 38px;
  height: 36px;
  background:  rgba(255, 255, 255);
  border: 1px solid rgba(242, 244, 255);
  border-radius: 12px;
  padding: 0 2px;
  display: flex;
  align-items: center;
  justify-content: center;

  :hover,
  :focus {
    cursor: pointer;
    outline: none;
    border: 1px solid rgba(6,46,245,0.30);
  }
`

const StyledMenu = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  text-align: left;
`

const MenuFlyout = styled.span`
  min-width: 8.125rem;
  background-color: ${({ theme }) => theme.bg3};
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
    0px 24px 32px rgba(0, 0, 0, 0.01);
  border-radius: 12px;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  position: absolute;
  top: 4rem;
  right: 0rem;
  z-index: 100;

  ${({ theme }) => theme.mediaWidth.upToMedium`
    top: -17.25rem;
  `};
`

const MenuItem = styled(ExternalLink)`
  flex: 1;
  padding: 0.5rem 0.5rem;
  color: ${({ theme }) => theme.text2};
  :hover {
    color: ${({ theme }) => theme.text1};
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
  }
`

const CODE_LINK = 'https://github.com/kswap-finance/kswap-interface'
const DOCS_LINK_ZH = 'https://docs.kswap.finance'
const DOCS_LINK_EN = 'https://docs.kswap.finance/v/en/'

export default function Menu() {
  // const { account } = useActiveWeb3React()

  const node = useRef<HTMLDivElement>()
  const open = useModalOpen(ApplicationModal.MENU)
  const toggle = useToggleModal(ApplicationModal.MENU)
  useOnClickOutside(node, open ? toggle : undefined)

  return (
    // https://github.com/DefinitelyTyped/DefinitelyTyped/issues/30451
    <StyledMenu ref={node as any}>
      <StyledMenuButton onClick={toggle}>
        <img src={MenuIcon} alt="menu"/>
      </StyledMenuButton>

      {open && (
        <MenuFlyout>
          {/* <MenuItem id="link" href="https://kswap.finance">
            <Info size={14} />
            About
          </MenuItem> */}
          <MenuItem id="link" href={i18next.language === 'zh-CN' ? DOCS_LINK_ZH : DOCS_LINK_EN}>
            <BookOpen size={14} />
            Docs
          </MenuItem>
          <MenuItem id="link" href={CODE_LINK}>
            <Code size={14} />
            Code
          </MenuItem>
          <MenuItem id="link" href="https://kswap-finance.medium.com/">
            <MessageCircle size={14} />
            Medium
          </MenuItem>
          <MenuItem id="link" href="https://t.me/kswap_finance">
            <MessageCircle size={14} />
            Telegram
          </MenuItem>
          <MenuItem id="link" href="https://twitter.com/kswap_finance">
            <MessageCircle size={14} />
            Twitter
          </MenuItem>
          {/* {account && (
            <ButtonPrimary onClick={openClaimModal} padding="8px 16px" width="100%" borderRadius="12px" mt="0.5rem">
              Claim KST
            </ButtonPrimary>
          )} */}
        </MenuFlyout>
      )}
    </StyledMenu>
  )
}
